import React from "react"
import { useState } from "react"
import deleteIcon from "../../../img/cart/delete.svg"
import cramCoat from "../../../img/catalog/cream_coat.png"
import incrementIcon from "../../../img/cart/increment.svg"
import decrementIcon from "../../../img/cart/decrement.svg"


function CardItem (props){
    let price = props.price
    const [sumValue, setSumValue] = useState (3)
    const [totalSum, setTotalSum] = useState(price * sumValue)
    function sumValueDecrement(){
        let decrement = sumValue - 1
        setSumValue(decrement)
        setTotalSum(price * decrement)
    }
        
    function sumValueIncrement (){
        let increment = sumValue + 1
        setSumValue(increment)
        setTotalSum(price * increment)
    }
    return(
        <li className="cart__item">
            <picture>
                <img className="cart__item__img" src={cramCoat} alt="" />
            </picture>
            <div className="cart__item__info">
                <h3 className="article__number">. 1589956</h3>
                <div className="other__info">
                    <h4 className="item__name"> </h4>
                    <p className="color"></p>
                    <p className="size">L</p>
                    <p className="sum">
                        <button className="decrement" onClick={sumValueDecrement}>
                            <img src={decrementIcon} alt="" />
                        </button>
                        <span className="sum__value">{sumValue}</span>
                        <button className="increment" onClick={sumValueIncrement}>
                            <img src={incrementIcon} alt="" />
                        </button>
                    </p>
                    <p className="price">{totalSum}</p>
                </div>
            </div>
            <button className="cart__item_delete">
                <img src={deleteIcon} alt="" />
            </button>
        </li>
    )
}
export default CardItem