import React, { useEffect } from "react"
import { useState } from "react"
import arrowDuwn from "../../../img/catalog/arrow_duwn.svg"
function ItemTab(props){
    const [openDescription, setOpenDescription] = useState(props.tabState)
    function toggleTab (){
        setOpenDescription(openDescription => !openDescription)
    }
    let descriptionState = openDescription ? " item__tab-active" : ""
    return(
        <li className={`item__tab ${descriptionState}`}>
            <h5 className="tab__title" onClick={toggleTab}>
                <span>{props.title}</span>
                <img className="tab__arrow" src={arrowDuwn} alt="" />
            </h5>
            <p className="tab__description">
                {props.description}
            </p>
        </li>
    )
}
export default ItemTab