import React from "react"
import { useState } from "react"
import { Link } from "react-router-dom"
import heart from "../../../img/catalog/heart.svg"

function CatalogCard (props){
    let color = props.color.map((i) => <li className="color_circle" style={{backgroundColor: i}}></li>)
    let size = props.size.map((ind) => <li className="size__item">{ind}</li>)
    let img1 = props.img1
    let img2 = props.img2
    const [image, setImage] = useState (img1)
    function hoverImage (){
        setImage(img2)
    }
    function leaveImage (){
        setImage(img1)
    }
    function selectCard (){
        localStorage.setItem("selectCard", JSON.stringify({
            title: props.title,
            description: props.description,
            price: props.price,
            composition : props.composition,
            size: props.size,
            color : props.color,
            colorDescription : props.colorDescription,
            img1 : props.img1,
            img2 : props.img2,
            img3 : props.img3,
            img4 : props.img4,
            img5 : props.img5,
        }))
    }
    return(
        <li className="catalod__card">
            <Link onClick={selectCard} to="/Page/Catalog/CatalogCardPage/CatalogCardPage">
                <picture>
                    <img onMouseEnter={hoverImage} onMouseLeave={leaveImage} className="card__img" src={"/yanki/img/catalog" + image} alt="" />
                </picture>
            </Link>
            <div className="card__info">
                <h4 className="card__title">{props.title}</h4>
                <h5 className="card__price">{props.price}</h5>
                <ul className="card__size">
                    {size}
                </ul>
                <ul className="card__color">
                    {color}
                </ul>
            </div>
            <button className="card__favorite">
                <img src={heart} alt="" />
            </button>
        </li>
    )
}
export default CatalogCard