import { useEffect } from "react";
import OrderTab from "./OrderTab";
function OrderHistory (props){
    const { load, setLoad } = props;
    useEffect(() => {
        setLoad(true);
        return () => {
          setLoad(false);
        };
      }, []);

    return(
        <ul className="order__history">
            <OrderTab tabState={false} data=" 718  28.09.21" status="" sum="13682 "/>
            <OrderTab tabState={false} data=" 328  10.07.21" status="" sum="7650 "/>
            <OrderTab tabState={true} data=" 235  03.05.21" status="" sum="7650 "/>
            <OrderTab tabState={false} data=" 213  01.05.21" status="" sum="7650 "/>
        </ul>
    )
}
export default OrderHistory