import React, { useEffect } from "react"
import { useState } from "react"
import PageItemSelect from "../../PageItemSelect/PageItemSelect"
import img from "../../../img/catalog/fur.png"
import arrowDuwn from "../../../img/catalog/arrow_duwn.svg"
import heart from "../../../img/catalog/heart2.svg"
import ItemTab from "./ItemTab"
import ItemImageMin from "./ItemImageMin"
function CatalogCardPage (){
    let selectCard = JSON.parse(localStorage.getItem("selectCard"))
    let colors = selectCard.color
    let color = colors.map((i) => <li className="color_circle" style={{backgroundColor: i}}></li>)
    const [imageMax , setImageMax] = useState(selectCard.img1)
    return(
        <section className="catalog__card__page">
            <PageItemSelect select=""/>
            <div className="item">
                <div className="item__slider">
                    <ul>
                        <ItemImageMin img={selectCard.img1} setImageMax={setImageMax}/>
                        <ItemImageMin img={selectCard.img2} setImageMax={setImageMax}/>
                        <ItemImageMin img={selectCard.img3} setImageMax={setImageMax}/>
                        <ItemImageMin img={selectCard.img4} setImageMax={setImageMax}/>
                        <ItemImageMin img={selectCard.img5} setImageMax={setImageMax}/>
                    </ul>
                    <picture>
                        <img className="image__max" src={"/yanki/img/catalog" + imageMax} alt="" />
                    </picture>
                </div>
                <div className="item__info">
                    <h2 className="item__title">{selectCard.title}</h2>
                    <h3 className="item__price">{selectCard.price}</h3>
                    <ul className="item__color__list">
                        {color}
                    </ul>
                    <p className="item__color__text">: {selectCard.colorDescription}</p>
                    <div className="item__size">
                        <span> </span>
                        <img src={arrowDuwn} alt="" />
                    </div>
                    <div className="item__buttons">
                        <button className="button__cart"> </button>
                        <button className="button__favorite">
                            <img className="button__favorite__icon" src={heart} alt="" />
                            <span> </span>
                        </button>
                    </div>
                    <h4 className="item__more"></h4>
                    <ul>
                        <ItemTab tabState={false} title="  " description={selectCard.description}/>
                        <ItemTab tabState={true} title="  " description={selectCard.composition}/>
                    </ul>
                </div>
            </div>
        </section>
    )
}
export default CatalogCardPage