import React from "react";
import { Link } from "react-router-dom";
import arrowDuwn from "../../../img/catalog/arrow_duwn.svg"
import CatalogCard from "./CatalogCard.jsx"
import {useSelector, useDispatch} from "react-redux"
import { selectGoods } from "../../../store/GoodsSlice";
function CatalogCards() {
  const goodsArr = useSelector(selectGoods)
  const dispatch = useDispatch()
  
  return (
    <div className="catalog__cards">
        <ul className="cards__filter">
            <li className="filter__item"><Link></Link><img src={arrowDuwn} alt=""/></li>
            <li className="filter__item"><Link></Link><img src={arrowDuwn} alt=""/></li>
            <li className="filter__item"><Link></Link><img src={arrowDuwn} alt=""/></li>
            <li className="filter__item"><Link> </Link><img src={arrowDuwn} alt=""/></li>
        </ul>
        <ul className="cards__list">
          {goodsArr.map(item => <CatalogCard
              title={item.title}
              price={item.price}
              description={item.description}
              size={item.size}
              color={item.color}
              colorDescription={item.colorDescription}
              composition={item.composition}
              img1={item.img1}
              img2={item.img2}
              img3={item.img3}
              img4={item.img4}
              img5={item.img5}
            />)}
        </ul>
    </div>
  );
}

export default CatalogCards;
