import React, {useState} from "react"
import { NavLink, Outlet } from "react-router-dom"
function PersonalAccountNavigation (props){
    let loadHistory = props.load
    let itemBg  = loadHistory ?  " personal__account__item-select": ""
    return (
        <ul className="personal__account__list">
            <li><NavLink to="OrderHistory/OrderHistory" className={`personal__account__item ${itemBg}`}> </NavLink></li>
            <li><NavLink to="PersonalData/PersonalData" className={({ isActive }) => (isActive ? 'personal__account__item personal__account__item-select' : 'personal__account__item')}> </NavLink></li>
            <li><NavLink to="Bonuses/Bonuses" className={({ isActive }) => (isActive ? 'personal__account__item personal__account__item-select' : 'personal__account__item')}> </NavLink></li>
            <li><NavLink className={({ isActive }) => (isActive ? 'personal__account__item personal__account__item-select' : 'personal__account__item')} to="/Page/New/New.jsx"></NavLink></li>
         </ul>
    )
}
export default PersonalAccountNavigation