import { useState, useEffect } from "react"
function ItemImageMin (props){
    let [stateImg , SetStateImg] = useState("flex")
    let setImageMax = props.setImageMax
    function setImg (){
        setImageMax(props.img)
    }
    function stateImageMin(){
        if (props.img == ""){
            SetStateImg("none")
        }
        else {
            SetStateImg("flex")
        }
    }
    useEffect(stateImageMin)
    return(
        <li onClick={setImg}style={{display: stateImg}}>
            <picture className="picture__image__min">
                <source srcSet=""/>
                <img className="image__min" src={"/yanki/img/catalog" + props.img} alt="" />
            </picture>
        </li>
    )
}
export default ItemImageMin