import React from "react"
import arrowDuwn from "../../../img/catalog/arrow_duwn.svg"
import { useState } from "react"
import creamCoat from "../../../img/catalog/cream_coat.png"
import bluePark from "../../../img/catalog/blue__park.png"
function OrderTab (props){
    const [openDescription, setOpenDescription] = useState(props.tabState)
    function toggleTab (){
        setOpenDescription(openDescription => !openDescription)
    }
    let descriptionState = openDescription ? " order__tab-active" : ""
    return(
        <li className={`order__tab ${descriptionState}`}>
            <header className="order__tab__header" onClick={toggleTab}>
                <h3 className="order__data">{props.data}</h3>
                <div className="order__status">
                    <h4 className="order__status__title">:</h4>
                    <p className="order__status__value">{props.status}</p>
                </div>
                <div className="order__sum">
                    <h4 className="order__sum__title"> :</h4>
                    <p className="order__sum__value">{props.sum}</p>
                </div>
                <img className="order__tab__icon" src={arrowDuwn} alt="" />
            </header>
            <main className="order__tab__main">
                <section className="order__tab__items">
                    <img className="tab__img" src={creamCoat} alt="" />
                    <div className="main__info">
                        <h4 className="order__article">. 1589956</h4>
                        <div className="order__item">
                            <h4 className="order__item__name"> </h4>
                            <span className="order__item__color" style={{backgroundColor: "#F1DDAA"}}></span>
                            <span className="order__item__size">: L</span>
                            <span className="order__item__quantity">: 3</span>
                            <span className="order__item__sum">9450 </span>
                        </div>
                    </div>
                </section>
                <section className="order__tab__items">
                    <img className="tab__img" src={bluePark} alt="" />
                    <div className="main__info">
                        <h4 className="order__article">. 1588262</h4>
                        <div className="order__item">
                            <h4 className="order__item__name"> </h4>
                            <span className="order__item__color" style={{backgroundColor: "#6F83A4"}}></span>
                            <span className="order__item__size">: M</span>
                            <span className="order__item__quantity">: 2</span>
                            <span className="order__item__sum">5800 </span>
                        </div>
                    </div>
                </section>
            </main>
        </li>
    )
}
export default OrderTab