import React from "react";
import { Link } from "react-router-dom";

function FooterUseful() {
  return (
    <div className="footer__useful">
        <h3 className="footer__company__title"></h3>
        <ul className="footer__company__list">
            <li className="footer__item"><Link to="/Page/Delivery/Delivery">  </Link></li>
            <li className="footer__item"><Link to="/Page/Refund/Refund"> </Link></li>
            <li className="footer__item"><Link to="/Page/PesonalAccount/PersonalAccount"> </Link></li>
        </ul>
    </div>
  );
}

export default FooterUseful;
