import React from "react"
import { Link } from "react-router-dom"
function HeaderAuthorization (props){
    let setAuthorize = props.setAuthorize
    function closeAutorization (e){
        e.preventDefault()
        setAuthorize(authorize => !authorize)
    }
    let setRegistration = props.setRegistration
    function registrationAccount(e){
        e.preventDefault()
        setAuthorize(authorize => !authorize);
        setRegistration(registration => !registration)
    }
    return(
        <form className={`authorization ${props.authorizationClassName}`}>
            <h3 className="authorization__title"></h3>
            <input className="authorization__input" type="email" placeholder=" e-mail*"/>
            <input className="authorization__input" type="password" name="" id="" placeholder=" *"/>
            <ul className="authorization__link">
                <li><Link> ?</Link></li>
                <li><Link onClick={registrationAccount}> ?</Link></li>
            </ul>
            <input className="authorization__submit" type="submit" value="" />
            <button onClick={closeAutorization} className="authorization__close">
                <div className="close__line"></div>
            </button>
        </form>
    )
}
export default HeaderAuthorization